.Track {
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba(256, 256, 256, 0.8);
}

.Track-action {
    cursor: pointer;
    padding: .5rem;
    font-size: 1.05rem;
    transition: color .25s;
    border: 0px;
    background-color: rgba(0, 0, 0, 0);
    color: #fff;
}

.Track-action:hover {
    color: rgba(265, 265, 265, .5);
}

.Track-information {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 72px;
    position: relative;
}

.Track-information h3 {
    margin-bottom: .22rem;
}

.Track-information h3 small {
    font-size: 0.8rem;
}

.Track-information p {
    font-size: .83rem;
    font-weight: 300;
    color: rgba(256, 256, 256, 0.8);
}

.Track-information audio {
    /* position: absolute; */
    bottom: 0;
    right: 0;
    width: 100%;
    /* height: 25px; */
}